import React from "react";
import contacts from "../contacts";
import Card from "./Card";

function App() {
  return (
    <div>
      <div className="heading">
        {" "}
        <h2>My Contacts</h2>
      </div>
      {contacts.map((contact) => (
        <Card
          name={contact.name}
          img={contact.imgURL}
          tel={contact.phone}
          email={contact.email}
        />
      ))}
    </div>
  );
}

export default App;
